import { Injectable } from '@angular/core';
import { IEmployee } from './Interface/IEmployee';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/Observable/throw';
//import 'rxjs/add/operator/toPromise';
import { Headers, RequestOptions } from '@angular/http';
//import { Post } from './post

@Injectable()
export class EmployeeServiceService {

  constructor(private _http: Http) {  }
  

	getEmployee(): Observable<IEmployee[]> {// show all record
		return this._http.get("http://vimalraj.somee.com/api/Employees")
			.map((response: Response) => <IEmployee[]>response.json())
			.catch(this.handleError);
	}


  getEmployeeByCode(id: number): Observable<IEmployee> {// show all record
    debugger;
    let url="http://vimalraj.somee.com/api/Employees/"
		return this._http.get(url + id)
			.map((response: Response) => <IEmployee>response.json())
			.catch(this.handleError);
	}
	


	deleteEmployeeById(empCode: number): Observable<string> {// show all record
		return this._http.post("http://vimalraj.somee.com/api/Employees/DeleteEmployee?Id=" + empCode, "", "")
			.map((response: Response) => response.toString())
			.catch(this.handleError);
	}

	insertEmployee(empID: number, empFname: string, empLname: string, empGender: string, empSalary: number, empStatus: string): Observable<string> {// insert record
		return this._http.post("http://vimalraj.somee.com/api/Employees/InsertEmployee?id=" + empID + "&fname=" + empFname + "&lname=" + empLname + "&gender=" + empGender + "&salary=" + empSalary + "&status=" + empStatus, "", "")
			.map((response: Response) => response.toString())
			.catch(this.handleError);
	}

	insertUserEmployee(userFname: string, userLname: string, userGender: string, userHAddress: string, userCity: string, userState: string, userZip: string, userEmail: string, userMob: string, userSalary: number): Observable<any> {// insert record
		let headers = new Headers({ 'Content-Type': 'application/json'});
		let options = new RequestOptions({ headers });
		return this._http.post("http://vimalraj.somee.com/api/UserRegistration?fname=" + userFname + "&lname=" + userLname + "&gender=" + userGender + "&haddress=" + userHAddress + "&city=" + userCity + "&state=" + userState + "&zip=" + userZip + "&email=" + userEmail + "&mob=" + userMob + "&salary=" + userSalary, "", "")
			.map((response: Response) => <any>response.json())
			.catch(this.handleError);
	}



	handleError(error: Response) {    // for Observable
		console.log(error);
		return Observable.throw(error);
	}
	
}



