import { Injectable } from '@angular/core';
import { IUser } from './Interface/IUser';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/Observable/throw';
// //import 'rxjs/add/operator/toPromise';
import { Headers, RequestOptions } from '@angular/http';
import { HttpHeaders } from '@angular/common/http/src/headers';
import { AnonymousSubscription } from 'rxjs/Subscription';
import { Options } from 'selenium-webdriver';

import {ICompany} from './Interface/ICompany';
import {ICompanyProduct} from './Interface/ICompanyProduct';

@Injectable()
export class CompanyProductService {

  constructor(private _http: Http) {	}


  getCompany(): Observable<ICompany[]> {
		return this._http.get('http://vimalraj.somee.com/api/ComponyCumProduct')
			 .map((response: Response) => <ICompany[]>response.json())
			 .catch(this.handleError);
  }

  getProductsByCompany(id :number): Observable<ICompanyProduct[]> {
    var url="http://vimalraj.somee.com/api/ComponyCumProduct/";
		return this._http.get(url+id)
			 .map((response: Response) => <ICompanyProduct[]>response.json())
			 .catch(this.handleError);
  }

  handleError(error: Response) {    // for Observable
    console.log(error);
    return Observable.throw(error);
  }
}



