import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';
import { IEditorTemplate } from '../Interface/IEditorTemplate';
import { EditorTemplateService } from '../editor-template.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { CKEditorComponent } from 'ngx-ckeditor';
import * as $ from 'jquery';
import { UserService } from '../user.service';

declare const CKEDITOR: any;

@Component({
  selector: 'app-editor-template',
  templateUrl: './editor-template.component.html',
  styleUrls: ['./editor-template.component.css']
})
export class EditorTemplateComponent implements OnInit {

  public editorValue: string = "";
  postedIEditorTemplate:IEditorTemplate;
  editorTemplateList: IEditorTemplate[];
  statusMessage: string = "Loading Data.... plz wait....";
  actionRecord: string="Add Record";
  viewDescription : string;
  viewCreatedBy: string;
  viewTempData: string;
  editId:number=0;
  editCreatedBy:string="";
  editDescription : string="";
  editEditorValue: string="";

  //@ViewChild("dd") Description: ElementRef;
  public ckConfig ={
    uiColor: '#99000',
    toolbarGroups: [
        { name: 'basicstyles', groups: ['basicstyles', 'cleanup'] },
        { name: 'paragraph', groups: ['list'] },
        { name: 'clipboard', groups: ['undo'] }
    ],
    removeButtons: 'Anchor,Outdent,Indent,Strike,Superscript,Subscript,PasteText,Cut,Copy,Paste,Scayt,PasteFromWord,SelectAll',
    forcePasteAsPlainText: true,
    extraPlugins: 'divarea',
    removePlugins: 'elementspath,resize',
    height: 250
}
public ckConfigEdit ={
  uiColor: '#99000',
  toolbarGroups: [
      { name: 'basicstyles', groups: ['basicstyles', 'cleanup'] },
      { name: 'paragraph', groups: ['list'] },
      { name: 'clipboard', groups: ['undo'] }
  ],
  removeButtons: 'Anchor,Outdent,Indent,Strike,Superscript,Subscript,PasteText,Cut,Copy,Paste,Scayt,PasteFromWord,SelectAll',
  forcePasteAsPlainText: true,
  extraPlugins: 'divarea',
  removePlugins: 'elementspath,resize',
  height: 150
}

  constructor(private _activatedRoute: ActivatedRoute, private location: Location,
    private _userServiceLogOut: UserService,
    private _editorTemplateService:EditorTemplateService,
    private _router: Router) { }

  ngOnInit() {

    this._editorTemplateService.getEditorTemplate()
          .subscribe((userData) => this.editorTemplateList = userData,
               (error) => {
                  this.statusMessage = "Problem with the service";
                  });

  }

getAllRecord(){
  this._editorTemplateService.getEditorTemplate()
          .subscribe((userData) => this.editorTemplateList = userData,
               (error) => {
                  this.statusMessage = "Problem with the service";
                  });

}
  onSubmit(model:any){
    
    var postedIEditorTemplate=<IEditorTemplate>{};
    postedIEditorTemplate.CreatedBy=model.CreatedBy;
    postedIEditorTemplate.Description=model.Description;
    postedIEditorTemplate.TemplateData=model.editor1;

    this._editorTemplateService.insertEditorTemplate(postedIEditorTemplate)
    .subscribe((userData) => this.statusMessage = userData,
    (error) => {
       this.statusMessage = "Problem with the service";
       });

       this.ngOnInit();
       this.ngOnInit();
       this.getAllRecord();
  }

  

 removeClick(event){
  for(let name in CKEDITOR.instances){
    CKEDITOR.instances[name].destroy(true);
  }  
  
 }

 rowClickTemplate($event){
  debugger;
  var row = $(event.target).closest('tr');
  var Id = row.find('td:eq(0)').text();
  this.viewDescription = row.find('td:eq(1)').text();
  this.viewCreatedBy = row.find('td:eq(2)').text();
  this.viewTempData = row.find('td:eq(3)').text();
  $("#viewTempData").html(this.viewTempData);
  $("#btnViewTemp").trigger("click");
 }

 btnEditClick(data:IEditorTemplate){
debugger;
  // var row = $(event.target).closest('tr');
  // this.editId =parseInt(row.find('td:eq(0)').text());
  // this.editDescription = row.find('td:eq(1)').text();
  // this.editCreatedBy= row.find('td:eq(2)').text();
  // this.editEditorValue = row.find('td:eq(3)').text();
  this.editId =data.TemplateID;
  this.editDescription = data.Description;
  this.editCreatedBy= data.CreatedBy;
  this.editEditorValue = data.TemplateData;
  
  $("#btnEditTemp").trigger("click");
 }

 onSubmitEdit(model:any){
    debugger;
  var id=this.editId;
  var postedIEditorTemplate=<IEditorTemplate>{};
  postedIEditorTemplate.TemplateID=this.editId;
  postedIEditorTemplate.CreatedBy=model.editCreatedBy;
  postedIEditorTemplate.Description=model.editDescription;
  postedIEditorTemplate.TemplateData=model.editEditor1;
//$("#exampleModalEdit").attr("data-dismiss","model");
  this._editorTemplateService.editEditorTemplate(id,postedIEditorTemplate)
  .subscribe((userData) => this.statusMessage = userData,
  (error) => {
     this.statusMessage = "Problem with the service";
     });

     this.ngOnInit();
     this.ngOnInit();
     this.getAllRecord();
    // this.getAllRecord();
}
btnDeleteClick(data:IEditorTemplate){
let id=data.TemplateID;
this._editorTemplateService.deleteEditorTemplate(id)
.subscribe((userData) => this.statusMessage = userData,
(error) => {
   this.statusMessage = "Problem with the service";
   });

   this.ngOnInit();
   this.ngOnInit();
   this.getAllRecord();

}

logOut() {
  localStorage.removeItem('currentUser');
  this._userServiceLogOut.isUserLoggedIn = false;
  this._router.navigate(["/"]);
}

}//End of Class



