import { Component, OnInit, Input } from '@angular/core';
import { ICompany } from '../Interface/ICompany';
import { ICompanyProduct } from '../Interface/ICompanyProduct';
import { CompanyProductService } from '../company-product.service';
import { UserService } from '../user.service';
import { Router } from '@angular/router';
import * as $ from 'jquery';

@Component({
  selector: 'app-collapse-example',
  templateUrl: './collapse-example.component.html',
  styleUrls: ['./collapse-example.component.css']
})
export class CollapseExampleComponent implements OnInit {

  tOggle:boolean=false;
  compProducts: ICompany[];
  childcompProducts: ICompanyProduct[];
  childcompProducts1: ICompanyProduct[];
  statusMessage: string = "Loading Data.... plz wait....";
  collapsed1:boolean = true;

  constructor(private _compProductService: CompanyProductService,
    private _userServiceLogOut: UserService,
    private _userService: UserService, private _router: Router) {
	}

  ngOnInit() {
    this._compProductService.getCompany()
    .subscribe((productsData) => this.compProducts = productsData,
    (error) => {
      this.statusMessage = "Problem with the service";
    })
}

getProductByCompanyID(id:number){
  this._compProductService.getProductsByCompany(id)
  .subscribe((productsData) => this.childcompProducts = productsData,
  (error) => {
    this.statusMessage = "Problem with the service";
  })
}

btnClick(event){
  debugger;
  var id=event.target.id;
  var value = event.target.value;
  this.getProductByCompanyID(value);

  if(this.tOggle==true){
  $('.Company_'+value).hide();
  this.tOggle=false;
  }
  else{
    $('.Company_'+value).show();
    this.tOggle=true;
  }

}

logOut() {
  localStorage.removeItem('currentUser');
  this._userServiceLogOut.isUserLoggedIn = false;
  this._router.navigate(["/"]);
}

}// End of Class



