import { Injectable } from '@angular/core';

@Injectable()
export class UserService {

  public isUserLoggedIn;
	public userName;
	constructor() {
		this.isUserLoggedIn = false;
	}
	setUserLoggedIn() {
		this.isUserLoggedIn = true;
		this.userName = "vimal raj";
	}
	getUserLoggedIn() {
		return this.isUserLoggedIn;
	}
}


