import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';
import { IEmployee } from '../Interface/IEmployee';
import { EmployeeServiceService } from '../employee-service.service';

@Component({
  selector: 'app-employee-accordion',
  templateUrl: './employee-accordion.component.html',
  styleUrls: ['./employee-accordion.component.css']
})
export class EmployeeAccordionComponent implements OnInit {

  employee: IEmployee;
  statusMessage: string;

  constructor(private _activatedRoute: ActivatedRoute, private location: Location,
    private _employeeService:EmployeeServiceService,
    private _router: Router) { }


  ngOnInit() {
    debugger;
    let empID: number = this._activatedRoute.snapshot.params["id"];

      this._employeeService.getEmployeeByCode(empID)
          .subscribe((userData) => this.employee = userData,
              (error) => {
                this.statusMessage = "Problem with the service";
                });


  }




  goBack(): void {
    //this.location.back();
    this._router.navigate(["/cascade"]);
}


}//End of class



