import { Injectable } from '@angular/core';
import { IProductInfo } from './Interface/IProductInfo';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/Observable/throw';
// //import 'rxjs/add/operator/toPromise';
import { Headers, RequestOptions } from '@angular/http';
import { HttpHeaders } from '@angular/common/http/src/headers';
import { AnonymousSubscription } from 'rxjs/Subscription';
import { Options } from 'selenium-webdriver';
@Injectable()
export class ProductInfoService {
  CreateProduct:IProductInfo;
  msg:string;
  constructor(private _http: Http) { }

  getProducts(): Observable<IProductInfo[]> {
		return this._http.get('http://vimalraj.somee.com/api/Product')
			 .map((response: Response) => <IProductInfo[]>response.json())
			 .catch(this.handleError);
  }

  insertProduct(files){
    debugger;
    let headers = new Headers({ 'Content-Type': 'multipart/form-data','Accept': 'application/json' });
    let options = new RequestOptions({ headers: headers });
    return  this._http.post("http://vimalraj.somee.com/api/Product/Insert",files)
             .map(response => response.json())
             .catch(error => Observable.throw(error));

}


  handleError(error: Response) {    // for Observable
    console.log(error);
    return Observable.throw(error);
  }
}



