import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/Observable/throw';
// //import 'rxjs/add/operator/toPromise';
import { Headers, RequestOptions } from '@angular/http';
import { HttpHeaders } from '@angular/common/http/src/headers';
import { AnonymousSubscription } from 'rxjs/Subscription';
import { Options } from 'selenium-webdriver';

import { ICountry } from './Interface/ICountry';
import { IRegion } from './Interface/IRegion';
import { IState } from './Interface/IState'

@Injectable()
export class CountryStateRegionService {

  constructor(private _http: Http) {	}

  getCountry(): Observable<ICountry[]> {
		return this._http.get('http://vimalraj.somee.com/api/CascadeCountry')
			 .map((response: Response) => <ICountry[]>response.json())
			 .catch(this.handleError);
  }

  getStateByCountryID(id :number): Observable<IState[]> {
    let url="http://vimalraj.somee.com/api/CascadeCountry/";
		return this._http.get(url+id)
			 .map((response: Response) => <IState[]>response.json())
			 .catch(this.handleError);
  }

  getRegionByStateID(id :number): Observable<IRegion[]> {
    let headers = new Headers({ 'Content-Type': 'application/json; charset=utf-8' });
    let options = new RequestOptions({ headers: headers});
    let url="http://vimalraj.somee.com/api/CascadeCountry";
		return this._http.post(url,id,options)
			 .map((response: Response) => <IRegion[]>response.json())
			 .catch(this.handleError);
  }

  handleError(error: Response) {    // for Observable
    console.log(error);
    return Observable.throw(error);
  }

}


