  constructor(private _http: Http) {	}

  getEditorTemplate(): Observable<IEditorTemplate[]> {
		return this._http.get('http://vimalraj.somee.com/api/Template')
			 .map((response: Response) => <IEditorTemplate[]>response.json())
			 .catch(this.handleError);
  }

  insertEditorTemplate(model :IEditorTemplate): Observable<string> {
    debugger;
     let headers = new Headers({ 'Content-Type': 'application/json; charset=utf-8' });
     let options = new RequestOptions({ headers: headers});

    return this._http.post("http://vimalraj.somee.com/api/Template",model,options)
			.map((response: Response) =>console.log(response.toString()))
			.catch(this.handleError);
  }
  
  editEditorTemplate(id: number,  model :IEditorTemplate): Observable<string> {// Edit record
    debugger;
    let headers = new Headers({ 'Content-Type': 'application/json; charset=UTS-8' });
     let options = new RequestOptions({ headers: headers});

    return this._http.put("http://vimalraj.somee.com/api/Template/"+id, model, options)
    .map((response: Response) =>console.log(response.toString()))
    .catch(this.handleError);
  }

  deleteEditorTemplate(id: number): Observable<string> {// Edit record
    debugger;
    let headers = new Headers({ 'Content-Type': 'application/json; charset=UTS-8' });
     let options = new RequestOptions({ headers: headers});

    return this._http.delete("http://vimalraj.somee.com/api/Template/"+id)
    .map((response: Response) =>console.log(response.toString()))
    .catch(this.handleError);
  }
  
