private  isValidFileExtension(files):number{
       var ext = files[0].name.toUpperCase().split('.').pop() || files[0].name;
       let extensions="JPG,JPEG,PNG,BMP,GIF,TIFF";
       var exists = extensions.indexOf(ext);
     return exists;
}


