import { Injectable } from '@angular/core';

@Injectable()
export class AlertService {
  private subject : string;
  constructor() { }

  success(message: string) {
    this.subject= message ;
  }
  
  error(message: string) {
    this.subject= message ;
  }
  
  getMessage(): string {
    return this.subject;
  }
  
}



