import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { UserService } from '../user.service';
import {IUser} from '../Interface/IUser';
import {IuserserviceService} from '../iuserservice.service';
import {AlertService} from '../alert.service';
@Component({
  selector: 'app-login-form',
  templateUrl: './login-form.component.html',
  styleUrls: ['./login-form.component.css']
})
export class LoginFormComponent implements OnInit {

  Users:IUser[];
  statusMessage: string;
  loading = false;

  constructor(private _router: Router, 
    private _userService: UserService, 
    private _iUserService: IuserserviceService,
    private _alertService: AlertService) {

    }

    ngOnInit() {
      this.statusMessage = this._alertService.getMessage();
      this._iUserService.getUsers() //good place bec service takes time to load
        .subscribe((usersData) => this.Users = usersData,
        (error) => {
          this.statusMessage = "Problem with the service";
        })
    }

    getUserNamePassword(userName: string, passWord: string): number {
      debugger;
      return this.Users.filter(e => e.UserName === userName && e.Password===passWord).length;
    }
    onSubmit(evt) {
      debugger;
     evt.preventDefault();
     var uname=evt.target.elements[0].value;
     var password=evt.target.elements[1].value;
    if(this.getUserNamePassword(uname,password)>=1){
      localStorage.setItem('currentUser', uname);
      this._userService.setUserLoggedIn();
      this._router.navigate(["dashboard"]);
    }
    else{
      this._alertService.error("Invalid Username Or Password");
      this.loading = false;
      this.statusMessage=this._alertService.getMessage();
    }
    }
  
  
  }
  



