import { Component, OnInit, Input } from '@angular/core';
import { IProductInfo } from '../Interface/IProductInfo';
import { ProductInfoService } from '../product-info.service';
import { UserService } from '../user.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-product-list',
  templateUrl: './product-list.component.html',
  styleUrls: ['./product-list.component.css']
})
export class ProductListComponent implements OnInit {
  selectedImage : string;
  products: IProductInfo[];
	statusMessage: string = "Loading Data.... plz wait....";
  constructor(private _productService: ProductInfoService,
    private _userService: UserService, private _router: Router) {
		//this.employees = this._employeeService.getEmployee();
	}

  ngOnInit() {
    this._productService.getProducts() //good place bec service takes time to load
    .subscribe((productsData) => this.products = productsData,
    (error) => {
      this.statusMessage = "Problem with the service";
    })
}

loadAllRecord(): void {
  this._productService.getProducts() //good place bec service takes time to load
    .subscribe((productsData) => this.products = productsData,
    (error) => {
      this.statusMessage = "Problem with the service";
    })
}

setSelectedImage(pdt:IProductInfo){
  this.selectedImage= pdt.productImage;	
}

logOut() {
  localStorage.removeItem('currentUser');
  this._userService.isUserLoggedIn = false;
  this._router.navigate(["/"]);
}

}



