import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { UserService } from './user.service';
import { Router } from '@angular/router';
import {AlertService} from './alert.service';
@Injectable()
export class AuthGuardGuard implements CanActivate {
  constructor(private _userService: UserService, private _router: Router,private _alertService:AlertService) { }
	canActivate(next: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean {
    debugger;
    if (localStorage.getItem('currentUser')) {
      return true;
		  // return this._userService.getUserLoggedIn();
     }
    else {
      this._alertService.error("Please Login to Access This Page");
      this._router.navigate(['/']);
      return false;
    }
    // 
    // console.log('you r not authorizad');
	}

  // constructor(private _userService:UserService){}
  // canActivate(
  //   next: ActivatedRouteSnapshot,
  //   state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {
  //   return this._userService.getUserLoggedIn();
  // }
}



