import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import { error } from 'util';
import * as $ from 'jquery';
//import {Accordion,AccordionGroup,AccordionHeading,AccordionModule,AccordionToggle} from 'ngx-accordion';
//InterFacess
import { IUser } from '../Interface/IUser';
import { ICountry } from '../Interface/ICountry';
import { IState } from '../Interface/IState';
import { IRegion } from '../Interface/IRegion';
import { IEmployee } from '../Interface/IEmployee';
//Servicess
import { CountryStateRegionService } from '../country-state-region.service';
import { EmployeeServiceService } from '../employee-service.service';
import { UserService } from '../user.service';
import { IuserserviceService } from '../iuserservice.service';

@Component({
  selector: 'app-cascade-drop-down',
  templateUrl: './cascade-drop-down.component.html',
  styleUrls: ['./cascade-drop-down.component.css']
})
export class CascadeDropDownComponent implements OnInit {

  country: ICountry[];
  state: IState[];
  region: IRegion[];
  userList: IUser[];
  employeeList: IEmployee[];
  currentC = 0;
  currentS = 0;
  currentR = 0;
  statusMessage: string = "Loading Data.... plz wait....";
  public editorValue: string = "";

  items = [{
    id: 1,
    name: "First",
    description: "First accordion"
}, {
    id: 2,
    name: "Second",
    description: "Second accordion"
}, {
    id: 3,
    name: "Third",
    description: "Third accordion"
}];

addItem() {
    this.items.push({
        id: this.items.length + 1,
        name: "Item #" + (this.items.length + 1),
        description: "Item #" + (this.items.length + 1) + " accordion"
    });
}



  constructor(private _countryStateRegionService: CountryStateRegionService,
    private _userServiceLogOut: UserService,
    private _userServiceList:IuserserviceService,
    private _employeeService:EmployeeServiceService,
    private _router: Router) { }

  ngOnInit() {
    this._countryStateRegionService.getCountry()
    .subscribe((productsData) => this.country = productsData,
    (error) => {
      this.statusMessage = "Problem with the service";
    });

    this._userServiceList.getUsers()
    .subscribe((userData) => this.userList = userData,
    (error) => {
      this.statusMessage = "Problem with the service";
    });

    this._employeeService.getEmployee()
    .subscribe((userData) => this.employeeList = userData,
    (error) => {
      this.statusMessage = "Problem with the service";
    });
      
   
    
  }

  getStateByCountryID(id:number){
    this._countryStateRegionService.getStateByCountryID(id)
    .subscribe((productsData) => this.state = productsData,
    (error) => {
      this.statusMessage = "Problem with the service";
    })
  }

  getRegionByStateID(id:number){
    this._countryStateRegionService.getRegionByStateID(id)
    .subscribe((productsData) => this.region = productsData,
    (error) => {
      this.statusMessage = "Problem with the service";
    })
  }


  onCountryChange(event){
    let newVal = event.target.value;
    console.log(newVal);
    let selectElementText = event.target['options'][event.target['options'].selectedIndex].text;
    console.log(selectElementText); 
    if(newVal>0){
      $("#ddlState").removeAttr("disabled");
      this.getStateByCountryID(newVal);
    }
    else{
      this.state=[];
      $("#ddlState").attr("disabled","disabled");
      this.region=[];
      $("#ddlRegion").attr("disabled","disabled");
    }
  }

  onStateChange(event){
    let newVal = event.target.value;
    console.log(newVal);
    let selectElementText = event.target['options'][event.target['options'].selectedIndex].text;
    console.log(selectElementText); 
    if(newVal>0){
      $("#ddlRegion").removeAttr("disabled");
      this.getRegionByStateID(newVal);
    }
    else{
      this.region=[];
      $("#ddlRegion").attr("disabled","disabled");
    }
  }

  onRegionChange(event){
    let newVal = event.target.value;
    console.log(newVal);
    let selectElementText = event.target['options'][event.target['options'].selectedIndex].text;
    console.log(selectElementText); 
  }


  onClick(event){
    debugger;
    alert(this.country.length);

  }


  logOut() {
    localStorage.removeItem('currentUser');
    this._userServiceLogOut.isUserLoggedIn = false;
    this._router.navigate(["/"]);
  }


  rowClick(event){
    debugger;
    var row = $(event.target).closest('tr');
    var Id = row.find('td:eq(0)').text();
    this._router.navigate(['/employee', Id]);
  }

  editorOnClick(event){
    debugger;
    
    console.log(this.editorValue);
  }
}  //End of Class


