import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpModule } from '@angular/http';
import { FormsModule }   from '@angular/forms';
import { AppComponent } from './app.component';
import { AuthGuardGuard } from './auth-guard.guard';
import { HeaderComponent } from './header/header.component';
import { LoginFormComponent } from './login-form/login-form.component';
import { FooterComponent } from './footer/footer.component';
import { Routes, RouterModule } from '@angular/router';
import { UserService } from './user.service';
import { AlertService } from './alert.service';
import {IuserserviceService} from './iuserservice.service';
import {CountryStateRegionService} from './country-state-region.service';
import {EmployeeServiceService} from './employee-service.service';
import {EditorTemplateService} from './editor-template.service';

import { CompanyProductService } from './company-product.service';
import { DashboardComponent } from './dashboard/dashboard.component';
import { RegisterComponent } from './register/register.component';
import { NotFoundComponent } from './not-found/not-found.component';
import { ProductInsertComponent } from './product-insert/product-insert.component';
import {ProductInfoService} from './product-info.service';
import { ProductListComponent } from './product-list/product-list.component';
import { UpLoadComponent } from './up-load/up-load.component';
import { CollapseExampleComponent } from './collapse-example/collapse-example.component';
import { CascadeDropDownComponent } from './cascade-drop-down/cascade-drop-down.component';

import {AccordionModule} from 'ngx-accordion';
import {CKEditorModule} from 'ngx-ckeditor';

import { EmployeeAccordionComponent } from './employee-accordion/employee-accordion.component';
import { EditorTemplateComponent } from './editor-template/editor-template.component';
declare let jQuery: Object;
const appRoute: Routes = [

  { path: '',           component: LoginFormComponent },
  { path: 'register',   component: RegisterComponent },
  { path: 'employee/:id',canActivate: [AuthGuardGuard], component: EmployeeAccordionComponent },
  { path: 'cascade',    canActivate: [AuthGuardGuard], component: CascadeDropDownComponent },
  { path: 'collapse',   canActivate: [AuthGuardGuard], component: CollapseExampleComponent },
  { path: 'product',    canActivate: [AuthGuardGuard], component: ProductInsertComponent },
  { path: 'dashboard',  canActivate: [AuthGuardGuard], component: DashboardComponent },
  { path: 'productList',canActivate: [AuthGuardGuard], component: ProductListComponent },
  { path: 'upload',     canActivate: [AuthGuardGuard], component: UpLoadComponent },
  { path: 'editorTemplate',canActivate: [AuthGuardGuard], component: EditorTemplateComponent },
  { path: '**', component: NotFoundComponent },
]
  


@NgModule({
  imports: [BrowserModule, RouterModule.forRoot(appRoute),HttpModule,FormsModule,
    AccordionModule,CKEditorModule,
	],
	declarations: [AppComponent, HeaderComponent, FooterComponent, LoginFormComponent, DashboardComponent, RegisterComponent, NotFoundComponent, ProductInsertComponent, ProductListComponent, UpLoadComponent, CollapseExampleComponent, CascadeDropDownComponent, EmployeeAccordionComponent, EditorTemplateComponent],
	bootstrap: [AppComponent],
  providers: [IuserserviceService,UserService,AuthGuardGuard,EmployeeServiceService,
    ProductInfoService,AlertService,CompanyProductService,EditorTemplateService,
    CountryStateRegionService]
})
export class AppModule { }



